.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.ant-btn-icon-only {
  width: 150px !important;
  height: 150px !important;
}

/* .ant-btn .anticon svg {
  width: 100px !important;
  height: 100px !important;
} */

.add_data_button {
  margin-right: 20px;
  margin-bottom: 20px;
  position: absolute;
  right: 0;
  bottom: 0;
}

.logo {
  margin: 0 auto;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%)
}

.popup {
  padding: 20px;
  width: 50%;
}

button.ant-btn {
  margin-top: 20px;
  width: 100%;
  height: 50px;
  font-size: 25px;
  text-transform: uppercase;
}

